import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Moment from 'react-moment';
import { connect } from 'react-redux';
import { deletePost } from '../../actions/post';
import path from 'path';

const PostItem = ({
  // showButton = true, vs propDefault?
  deletePost,
  auth,
  post: { _id, text, name, user, comments, postDate, imageUrl, type },
}) => {
  // console.log(user === auth.user._id);
  return (
    <div className='post bg-white p-1 my-1'>
      <div>
        <img src={imageUrl[0] ? '/' + imageUrl[0].url : null} alt='' />
      </div>
      <div>
        <p className='my-10'>{text}</p>
        <p className='post-date'>
          发布于：<Moment format='YYYY/MM/DD  h:mm:ss a'>{postDate}</Moment>
        </p>

        <Link to={`/posts/${type}/${_id}`} className='btn btn-primary'>
          查看详情
        </Link>

        {!auth.loading && user === (auth.user ? auth.user._id : null) && (
          <button
            type='button'
            className='btn btn-danger'
            onClick={() => deletePost(_id)}
          >
            <i className='fas fa-times'></i>
          </button>
        )}
      </div>
    </div>
  );
};

PostItem.propTypes = {
  post: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
  deletePost: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { deletePost })(PostItem);
