import React from 'react';
import './index.css';
import { Link } from 'react-router-dom';

function index({ content }) {
  return (
    <div className='Direction'>
      <Link to={'/posts/' + content}>
        {content}
        {/* <span className='D-S' onClick={() => onClick()}>
        </span> 这里直接去post就可以了。 */}
      </Link>
    </div>
  );
}

export default index;
