import React, { useState, Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getPosts, changePage, loadingf } from '../../actions/post';
import Spinner from '../layout/spinner';
import PostItem from './postItem.js';

import './getPosts.css';

//get posts of a certain type
const GetPosts = ({
  loadingf,
  getPosts,
  changePage,
  post: { posts, loading, current, count },
  zipCode,
  match,
}) => {
  const [formData, setFormData] = useState({
    pricemin: '',
    pricemax: '',
    yearmin: '',
    yearmax: '',
    odomax: '',
  });

  //搜索参数
  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
    console.log(formData);
  };

  const { pricemax, pricemin, yearmin, yearmax, odomax } = formData;

  //发送搜索请求
  const sortPost = () => {
    loadingf();
    const query = JSON.stringify({
      pricemax,
      pricemin,
      yearmin,
      yearmax,
      odomax,
      type,
    });
    console.log(query);
    getPosts({ query });
  };

  //进入页面按类加载
  let type = match.params.type;
  useEffect(() => {
    const query = JSON.stringify({ type: type, fil: null, page: current });
    console.log(current);
    getPosts({ query });
  }, [getPosts]);

  //翻页
  const changeP = (nop) => {
    loadingf();
    const query = JSON.stringify({
      type: type,
      fil: null,
      page: current + nop,
    });
    changePage({ query });
  };

  return loading ? (
    <Spinner />
  ) : (
    <section className='container'>
      <p className='hp'>
        {zipCode}
        <span className='hps'> 附近的{type}信息</span>
      </p>
      {type === '二手车' ? (
        <div className='sd-1'>
          <div className='sd-2'>
            <p className='sp-1'>
              价格
              <span className='ss-1'>
                <span className='ss-2'>
                  最低{' '}
                  <input
                    name='pricemin'
                    type='text'
                    onChange={(e) => {
                      onChange(e);
                    }}
                    className='sb-1'
                  />{' '}
                </span>
                <span className='ss-3'>
                  最高{' '}
                  <input
                    name='pricemax'
                    onChange={(e) => {
                      onChange(e);
                    }}
                    type='text'
                    className='sb-2'
                  />
                </span>
              </span>
            </p>
            <p className='sp-1'>
              年份
              <span className='ss-1'>
                <span className='ss-2'>
                  最早{' '}
                  <input
                    name='yearmin'
                    onChange={(e) => {
                      onChange(e);
                    }}
                    type='text'
                    className='sb-1'
                  />{' '}
                </span>
                <span className='ss-3'>
                  最晚{' '}
                  <input
                    name='yearmax'
                    onChange={(e) => {
                      onChange(e);
                    }}
                    type='text'
                    className='sb-2'
                  />
                </span>
              </span>
            </p>
            {/* <p className='sp-2'>
              厂商
              <span className='ss-1'>
                <span className='ss-2'>
                  <input type='text' className='sb-2' />
                </span>
              </span>
            </p> */}
            <p className='sp-2'>
              里程
              <span className='ss-1'>
                <span className='ss-2'>
                  最大{' '}
                  <input
                    onChange={(e) => {
                      onChange(e);
                    }}
                    name='odomax'
                    type='text'
                    className='sb-2'
                  />
                </span>
              </span>
            </p>
          </div>
          <div className='sd-3'>
            <button onClick={sortPost}>筛选</button>
          </div>
        </div>
      ) : null}
      {/* Postform */}
      <div className='posts'>
        {posts.map((
          post //?
        ) => (
          <PostItem key={post._id} post={post} />
        ))}
      </div>
      <div>
        <button
          onClick={() => changeP(-1)}
          className='btn btn-primary'
          disabled={current === 1 ? true : false}
        >
          &lt;&lt;
        </button>
        <button
          className='btn btn-primary'
          onClick={(nop) => changeP(1)}
          style={{ float: 'right' }}
          disabled={count / 5 >= current ? false : true}
        >
          &gt;&gt;
        </button>
      </div>
    </section>
  );
};

GetPosts.propTypes = {
  getPosts: PropTypes.func.isRequired,
  changePage: PropTypes.func.isRequired,
  post: PropTypes.object.isRequired,
  zipCode: PropTypes.number.isRequired,
  loadingf: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  post: state.post,
  zipCode: state.zipCode.zipCode,
});
export default connect(mapStateToProps, { getPosts, changePage, loadingf })(
  GetPosts
);
