import axios from 'axios';
import React, { useState, Fragment } from 'react';
import { connect } from 'react-redux';

const SendPost = () => {
  const [formData, setFormData] = useState({
    text: '',
    zipCode: '',
    type: '',
    model: '',
    make: '',
    year: '',
    odo: '',
  });

  const { text, zipCode } = formData;

  //二手车
  const { price, year, make, odo } = formData;

  // 租房
  //price

  //求助

  //闲置
  //price

  let { type } = formData;

  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const onSubmit = async (e) => {
    e.preventDefault();

    const newPost = { text, zipCode, year, make, odo, price, type };
    console.log(newPost);
    try {
      const config = {
        headers: {},
      };
      const data = new FormData();
      const files = document.querySelector('#file').files;
      for (let i = 0; i <= files.length; i++) {
        data.append('files', files[i]);
      }
      // console.log(data);
      data.append('json', JSON.stringify(newPost));
      // for (let key of data.entries()) {
      //   console.log(key[0] + ', ' + key[1]);
      // }
      console.log(type);
      const res = await axios.post('/api/post', data, config);
      console.log(res.data);
    } catch (err) {
      console.log(err.response.data);
    }
  };

  return (
    <section className='container'>
      <div className='post-form'>
        <div className='bg-primary p'>
          <h3>发布信息</h3>
        </div>
        <form className='form my-1' action='/' onSubmit={(e) => onSubmit(e)}>
          <input
            type='text'
            name='type'
            placeholder='租房/二手车/闲置/求助'
            onChange={(e) => {
              onChange(e);
            }}
          />
          <textarea
            name='text'
            cols='30'
            rows='5'
            placeholder=''
            onChange={(e) => {
              onChange(e);
            }}
            required
          ></textarea>
          <input
            type='text'
            name='zipCode'
            value='00000'
            onChange={(e) => {
              onChange(e);
            }}
          />
          {type === '闲置' || type === '租房' || type === '二手车' ? (
            <input
              type='text'
              name='price'
              placeholder='价格'
              onChange={(e) => {
                onChange(e);
              }}
              required
            />
          ) : null}
          {type === '二手车' ? (
            <Fragment>
              <input
                type='text'
                name='year'
                placeholder='年份'
                onChange={(e) => {
                  onChange(e);
                }}
              />{' '}
              <input
                type='text'
                name='make'
                placeholder='厂商'
                onChange={(e) => {
                  onChange(e);
                }}
              />
              <input
                type='text'
                name='odo'
                placeholder='里程'
                onChange={(e) => {
                  onChange(e);
                }}
              />
            </Fragment>
          ) : null}

          <input
            className='btn btn-dark my-1'
            type='button'
            value='插入图片'
            onClick={() => document.getElementById('file').click()}
          />

          <input
            type='file'
            style={{ display: 'none' }}
            name='test'
            id='file'
            multiple
          />
          <input type='submit' className='btn btn-dark my-1' value='发布' />
        </form>
      </div>
    </section>
  );
};

// const mapStateToProps = (state) => ({
//   user: state.auth.user,
// }); //user is not needed cuz the axios global header.

export default SendPost;
