import axios from 'axios';
import { setAlert } from './alert';
import {
  GET_POSTS,
  POST_ERROR,
  CHANGE_PAGE,
  LOADING,
  DELETE_POST,
  GET_POST,
} from './types';

//get posts
export const getPosts = ({ query }) => async (dispatch) => {
  try {
    const res = await axios.get(`/api/post/${query}`); //这里query必须JSON格式
    console.log(query);
    dispatch({
      type: GET_POSTS,
      payload: res.data,
    });
  } catch (err) {
    dispatch({
      type: POST_ERROR,
      payload: { msg: err.response.statusText, status: err.response.status },
    });
  }
};

