import React, { Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Spinner from '../layout/spinner';
import { getPost } from '../../actions/post';
import { Link } from 'react-router-dom';
import Moment from 'react-moment';
import { deletePost } from '../../actions/post';

function Post({ auth, getPost, post: { post, loading }, match }) {
  useEffect(() => {
    const query = JSON.stringify({ id: match.params.id });
    getPost({ query });
  }, [getPost]);

  if (loading || post === null) {
    return <Spinner />;
  }
  //post===null气死我了
  else {
    const { _id, text, name, user, comments, postDate, imageUrl, type } = post;
    return (
      <section className='container'>
        <Link to={`/posts/${type}`} className='btn'>
          返回
        </Link>
        <div className='post bg-white p-1 my-1'>
          <div>
            <img src={imageUrl[0] ? '/' + imageUrl[0].url : null} alt='' />
          </div>
          <div>
            <p className='my-10'>{text}</p>
            <p className='post-date'>
              发布于：<Moment format='YYYY/MM/DD  h:mm:ss a'>{postDate}</Moment>
            </p>
            {/* <Link to={`/posts/${type}/${_id}`} className='btn btn-primary'>
              查看详情
            </Link> */}
            {/* 两种模式，一种是link to, 在新route发送请求，另一种是button, click发送请求。如果Linkto然后click发送请求，而不在route useeffect发送请求，哪个好？ */}
            {!auth.loading && user === (auth.user ? auth.user._id : null) && (
              <button
                type='button'
                className='btn btn-danger'
                onClick={() => deletePost(_id)}
              >
                <i className='fas fa-times'></i>
              </button>
            )}
          </div>
        </div>
      </section>
    );
  }
}

Post.propTypes = {
  getPost: PropTypes.func.isRequired,
  post: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({ post: state.post, auth: state.auth });
export default connect(mapStateToProps, { getPost })(Post);
